<template>
  <div id="adasd">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <h2>PDF</h2>
  </div>
</template>

<script>
  export default {
    name: "financialTimesDetail",
    data() {
      return {
        title: '',
      };
    },
    methods: {
      onClickLeft() {
        this.$router.back();
      },
    },
    created() {
      this.title = this.$route.params.id;
    }
  }
</script>

<style scoped>

</style>



